:root {
  --font-color: #333;
  --font-color-light: rgb(73, 71, 71);
  --background-color: #eee;
  --background-color-light: rgb(255, 255, 255);
  --link-color: cornflowerblue;
}

[data-theme="dark"] {
  --font-color: #eee;
  --font-color-light: rgb(255, 255, 255);
  --background-color: #333;
  --background-color-light: rgb(73, 71, 71);
  --link-color: lightblue;
}

body {
  background-color: var(--background-color);
  color: var(--font-color);
}

a {
  color: var(--link-color);
}

/* Custom Dark Mode Toggle Element */

.toggle-theme-wrapper {
  display: flex;
  flex-direction: row;
  align-items: center;
  padding-right: 1rem;
}

.toggle-theme-wrapper span {
  font-size: 28px;
  margin: 0 4px;
}

.toggle-theme {
  position: relative;
  display: inline-block;
  height: 34px;
  width: 60px;
}

.toggle-theme input {
  display: none;
}

.slider {
  background-color: #ccc;
  position: absolute;
  cursor: pointer;
  bottom: 0;
  left: 0;
  right: 0;
  top: 0;
  transition: 0.2s;
}

.slider:before {
  background-color: #fff;
  bottom: 4px;
  content: "";
  height: 26px;
  left: 4px;
  position: absolute;
  transition: 0.4s;
  width: 26px;
}

input:checked+.slider:before {
  transform: translateX(26px);
}

input:checked+.slider {
  background-color: cornflowerblue;
}

.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}


@media only screen and (max-width: 1024px) {
  .slider:before {
    background-color: #fff;
    bottom: 2px;
    content: "";
    height: 20px;
    left: -8px;
    position: absolute;
    transition: 0.4s;
    width: 20px;
  }

  .toggle-theme {
    position: relative;
    display: inline-block;
    height: 24px;
    width: 40px;
  }

  input:checked+.slider:before {
    transform: translateX(20px);
  }
}